.app{
    background: black;
}
.header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 50px;
    flex-wrap: wrap;
    width: 100%;
}
.header>h1{
    font-size: 50px;
    color: white;
}
.header i{
    color: darkgoldenrod;
}

.search-form{
    display: flex;
    align-items: center;
    flex-direction: column;
    background:coral;
    width: 80%;
    height: auto;
    margin: auto;
    padding: 50px;
    margin-bottom: 50px;
    border-radius: 30px;
    box-shadow: 2px 2px grey;
}
.search-form form>input{
  height: 80px;
  border: 1px solid black;
  font-size: 16px;
  width: 100%;
  border-radius: 10px;
  padding: 10px;
  margin: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}
.search-form form>button{
    background: black;
    color: white;
    border: 1px solid white;
    border-radius: 20px;
    width:70%;
    font-size: 30px;
    padding: 10px;
    margin-bottom: 40px;
    margin-left: 50px;
    margin-top: 30px;
}
.footer{
    background: grey;
    color: white;
    height: auto;
    width: 100%;
    text-align: center;
}
.footer p{
    margin: auto;
    padding:30px;
    font-size: 25px;
}



img {
    height: auto;
    max-width: 100%;
    vertical-align: middle;
  }
  
  .btn {
    background-color: white;
    border: 1px solid lightgray;
    border-radius: 1rem;
    color: darkgray;
    padding: 0.5rem;
    /* text-transform: lowercase; */
  }
  
  .btn--block {
    display: block;
    width: 100%;
  }
   
  .cards {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .cards__item {
    display: flex;
    padding: 1rem;
    @media(min-width: 40rem) {
      width: 50%;
    }
    @media(min-width: 56rem) {
      width: 33.3333%;
    }
  }
  
  .card {
    background-color: white;
    border-radius: 0.25rem;
    box-shadow: 0 20px 40px -14px rgba(0,0,0,0.25);
    display: flex;
    flex-direction: column;
    overflow: hidden;
    /* &.:hover {
      .card__image {
        filter: contrast(100%);
      } */
    }
  
  .card__content {
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    padding: 1rem;
  }
  
  .card__image {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-top-left-radius: 0.25rem;
    border-top-right-radius: 0.25rem;
    filter: contrast(70%);
    /* //filter: saturate(180%); */
    overflow: hidden;
    position: relative;
    transition: filter 0.5s cubic-bezier(.43,.41,.22,.91);;
    /* &::before {
      content: "";
        display: block;
      padding-top: 56.25%; // 16:9 aspect ratio
    } */
    @media(min-width: 40rem) {
      &::before {
        padding-top: 66.6%;
      }
    }
  }
  
